import { Pipe, PipeTransform } from '@angular/core';
import {Variable} from "../../models/variable.model"

/**
 * Generated class for the VariableValuePipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
  name: 'variableValue', 
})
export class VariableValuePipe implements PipeTransform {
  transform(value : any, args : {variable : Variable}) {
    if(args.variable.possible_values.length > 0){
    	return value != undefined ? args.variable.possible_values.find(x => x.value == value).label : undefined;
    }else
   	 	return value;
  }
}



