import { Component, Input,
 OnInit, OnDestroy, NgZone } from '@angular/core';
import  { ModalController} from "ionic-angular"
import {Variable, RecordItem} from "../../models/variable.model"
import {SensorProvider} from "../../providers/sensor/sensor.provider";
import { take } from 'rxjs/operators';
import {Subscription} from "rxjs";

/**
 * Generated class for the ParameterComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */

@Component({
  selector: 'parameter',
  templateUrl: 'parameter.html',
  host: {
        'class': 'item item-block'
    }
})
export class ParameterComponent  implements OnInit, OnDestroy {

  @Input()
  parameter : Variable;
  type : string="";
  sensorMessageSubscription : Subscription;

  constructor(
        private sensorProvider : SensorProvider,
        private zone : NgZone,
        private modal : ModalController
    ){
  	//super(form, config, elementRef, renderer, reorder);
  }

  ngOnInit(){
    switch (this.parameter.type) {
      case "UIntegerT":
      case "IntegerT" : {
        if(this.parameter.possible_values.length>0)
          this.type = 'FixedInteger';
        else
          this.type = 'Integer';
      } break; 
      
      default:
          this.type = this.parameter.type;
        break;
    }
    if(!(this.parameter instanceof RecordItem) && this.parameter.readable ){
      this.askForVariableState()
      this.registerToSensorStateUpdates();
    }
  }

  ngOnDestroy(){
    if(this.sensorMessageSubscription)
      this.sensorMessageSubscription.unsubscribe();
  }

  askForVariableState(){
    this.sensorProvider.askForMenuVariableState(this.parameter)
  }

  registerToSensorStateUpdates(){
      this.sensorMessageSubscription = this.sensorProvider.sensorReadMessage.subscribe(s => {
        if( s === undefined || s === null || this.sensorProvider.registry[this.parameter.index] == undefined )
          return;
        if(this.sensorProvider.registryToRead === this.parameter.index){
             this.zone.run(() => {
              this.parameter.readValue(this.sensorProvider.registry[this.parameter.index]);
              this.sensorProvider.messageReceived++;
              this.sensorProvider.messageConsumed.next(true);
            });
        }else
        {
          var j=0
          var t = j+1;
        }
      });
  }

  updateSensor(){
    var i=0;
    if(this.parameter instanceof RecordItem)
      this.sensorProvider.updateSensorState(this.parameter.parent.index,this.parameter.writeValue()); 
    else
      this.sensorProvider.updateSensorState(this.parameter.index,this.parameter.writeValue()); 
  }

  numericTouched(){
    var number_input = this.modal.create('NumberInputPage', {'title' : this.parameter.name, 'value' : this.parameter.value});
    number_input.onDidDismiss(data => {
      if(data.save == true){
        this.parameter.value=data.value;
        this.updateSensor();
      }
    });
    number_input.present();
  }

}



