import { Component, ViewChildren, QueryList, Input } from '@angular/core';
import {Variable} from "../../models/variable.model"
import {ItemSliding} from "ionic-angular"
import {ShortcutsProvider} from "../../providers/shortcuts/shortcuts.provider";

/**
 * Generated class for the ParameterListComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'parameter-list',
  templateUrl: 'parameter-list.html'
})
export class ParameterListComponent {

@Input()
parameters : Variable[];

   @ViewChildren(ItemSliding) 
 items: QueryList<ItemSliding>;

  constructor(private shortcuts: ShortcutsProvider) {
  }

  homeClicked(par : Variable, index){
  	this.items.find((x,_index) => _index === index).close();
  	par.hidden=false;
  	this.shortcuts.addShortCut(par);
  }

}



