import {Variable} from "./variable.model";

export class Sensor{
	state : string  = "1400026700333034534500014F00000731324200";
	public config : any;
	public analogalueSubIndex = 1;

	constructor(){
	}

	configure(xml : any){
		this.config = xml;
	}



}

export class ParameterGroup{
	name : string;
	parameters : Variable[];

	constructor(obj ? : { name? : string, xmlGlobalConfig  : any, xmlStandardConfig :any}){
		this.parameters = [];
		if(obj){
			let me = this.findMe(obj.name, obj.xmlGlobalConfig);
			if(me.Name)
				this.name = Variable.translateString(obj.xmlGlobalConfig.IODevice , me.Name[0].$.textId);
			else
				this.name =  Variable.translateString(obj.xmlGlobalConfig.IODevice , me.$.id);
			if(me.VariableRef){
				for(let variableRef of me.VariableRef){
					let variableId = variableRef.$.variableId;
					let variable= obj.xmlGlobalConfig.IODevice.ProfileBody["0"].DeviceFunction["0"].VariableCollection["0"].Variable.find(x=>x.$.id === variableId);
					// TODO: case with variable in StdVariableRef
					if(variable){
						//if(variable.DatatypeRef){ 
						//		variable = obj.xmlGlobalConfig.IODevice.ProfileBody["0"].DeviceFunction["0"].DatatypeCollection["0"].Datatype.find(x => x.$.id==variable.DatatypeRef[0].$.datatypeId);
						//		this.parameters.push(new Variable({name : me.Name[0].$.textId, xmlLocalConfig : variable, xmlGlobalConfig : obj.xmlGlobalConfig}));
						//}else{
							this.parameters.push(new Variable( {xmlLocalConfig : variable,
							 xmlGlobalConfig : obj.xmlGlobalConfig.IODevice}));
						//}
					}else{
						let standardVariableObj = obj.xmlGlobalConfig.IODevice.ProfileBody["0"].DeviceFunction["0"].VariableCollection["0"].StdVariableRef.find(x=>x.$.id === variableId);
						if(standardVariableObj){
							standardVariableObj= obj.xmlStandardConfig.IODDStandardDefinitions.VariableCollection["0"].Variable.find(x => x.$.id === standardVariableObj.$.id)
							try{
								var standardVariable = new Variable( {xmlLocalConfig : standardVariableObj,
							 xmlGlobalConfig : obj.xmlStandardConfig.IODDStandardDefinitions});
							this.parameters.push(standardVariable);
							} catch(e){}
							
						}
					}
				}
			}
		}
	}

	protected findMe(name : string, config : any){
			return config.IODevice.ProfileBody["0"].DeviceFunction["0"].UserInterface["0"].MenuCollection["0"].Menu.find(x => x.$.id === name)
	}
}

export class ParameterDataLevel1 extends ParameterGroup{
		children : ParameterGroup[];

		constructor(obj ? : { xmlGlobalConfig  : any, xmlMenuConfig : any, xmlStandardConfig :any} ){
			super({name : obj.xmlMenuConfig.$.id ? obj.xmlMenuConfig.$.id : obj.xmlMenuConfig.$.menuId,
			 xmlGlobalConfig : obj.xmlGlobalConfig,
			 xmlStandardConfig : obj.xmlStandardConfig});
			this.children = [];
			let id = obj.xmlMenuConfig.$.id ? obj.xmlMenuConfig.$.id : obj.xmlMenuConfig.$.menuId;
			let me = this.findMe(id,obj.xmlGlobalConfig);
			if(me.MenuRef){
					for(let group of me.MenuRef){
						let submenu = group.$.menuId;
						this.children.push(new ParameterGroup({name : submenu, 
							xmlGlobalConfig : obj.xmlGlobalConfig,
							xmlStandardConfig : obj.xmlStandardConfig}));
					}
				}
		}

		public getVariables(){
			var retval=this.parameters;
			for( let child of this.children)
				retval = retval.concat(child.parameters);
			return retval;
		}
}

export class ParameterDataLevel2 extends ParameterGroup{
	children : ParameterDataLevel1[];
	constructor(obj ? : { xmlGlobalConfig  : any, xmlMenuConfig : any, xmlStandardConfig :any}){
		super({name : obj.xmlMenuConfig.$.id, xmlGlobalConfig : obj.xmlGlobalConfig, xmlStandardConfig : obj.xmlStandardConfig});
		this.children = [];
		let me = this.findMe(obj.xmlMenuConfig.$.id,obj.xmlGlobalConfig);
		if(me.MenuRef){
			for(let group of me.MenuRef){
					this.children.push(new ParameterDataLevel1({xmlMenuConfig : group,
					 xmlGlobalConfig : obj.xmlGlobalConfig, 
					 xmlStandardConfig : obj.xmlStandardConfig}));
				}
			}
	}
			public getVariables(){
			var retval=this.parameters;
			for( let child of this.children)
				retval = retval.concat(child.getVariables());
			return retval;
		}
}

export class SensorMessage{
	registry : number;
	message : string;

	constructor(registry : number, message : string){
		this.registry = registry;
		this.message = message;
	}
}


