import { Component, Input, OnChanges } from '@angular/core';
import {trigger ,transition, style, animate, state} from "@angular/animations"
import {Variable} from "../../models/variable.model"

/**
 * Generated class for the SensorPlotComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'sensor-plot',
  templateUrl: 'sensor-plot.html',
  animations: [
    trigger('shift', [
      state('true', style({ 'margin-left': "{{ marginLeft }}%" }), { params : { marginLeft : 100.0}}),
      state('false', style({ 'margin-left': "{{ marginLeft }}%" }), { params : { marginLeft : 100.0}}),
      transition('* => *', [
        animate("1s ease-out",
        style({ 'margin-left': "{{ marginLeft }}%" }))
      ]) 
    ])
  ]
})
export class SensorPlotComponent implements OnChanges{

  @Input()
  distance : any;
  animate : boolean = false;
  plot_img_margin = 9.2
  plot_img_max_margin= 100-this.plot_img_margin;

  constructor() {
  }

  ngOnChanges(){
  	this.animate = !this.animate;
  	if(this.distance === undefined)
  		this.distance=0;
  }

  log(n : number){
    return Math.log10(n);
  }

}



