import { Component, Input, Output,EventEmitter } from '@angular/core';
import {Variable} from "../../models/variable.model"

/**
 * Generated class for the ProcessDataComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'process-data',
  templateUrl: 'process-data.html'
})
export class ProcessDataComponent {

  @Input()
  data : Variable[] = [];
  @Input()
  mode : string;
  @Output()
  accordionOpened : EventEmitter<boolean> = new EventEmitter<boolean>();
  accordionOpen : boolean = false;

  constructor() {

  }

  toggleAccordion(){
    this.accordionOpen = !this.accordionOpen;
    this.accordionOpened.emit(this.accordionOpen);
  }
}



