import { Injectable } from '@angular/core';
import {BehaviorSubject} from "rxjs";
import {Variable} from "../../models/variable.model";

/*
  Generated class for the ShortcutsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ShortcutsProvider {

_shortcuts : Variable[] = [];
shortcuts : BehaviorSubject<Variable[]> = new BehaviorSubject<Variable[]>(this._shortcuts);

  constructor() {
  }

  addShortCut(elem : Variable) : void {
  	this._shortcuts.push(elem);
  	this.update();
  }

  private update(){
  	this.shortcuts.next(this._shortcuts);
  }

}



