import { Component, ViewChild } from '@angular/core';
import { Platform, Nav } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import {BackgroundMode} from '@ionic-native/background-mode';
import {SensorProvider} from "../providers/sensor/sensor.provider"
import {Subscription} from "rxjs"

declare var cordova : any;
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = 'HomePage';

  @ViewChild(Nav) nav: Nav
  backgroundSubscription : Subscription;

  constructor(private platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen,
   private backgroundMode : BackgroundMode, private sensorProvider:  SensorProvider) {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      if(this.backgroundSubscription != undefined)
        this.backgroundSubscription.unsubscribe();
      statusBar.styleDefault();
      statusBar.overlaysWebView(false);
      splashScreen.hide();
      if(this.platform.is('cordova')){
        this.backgroundMode.configure({silent : true, hidden : true});
        this.backgroundMode.enable();
        this.backgroundMode.setDefaults({silent : true, hidden : true});
        this.backgroundSubscription = this.backgroundMode.on('deactivate').subscribe( () => {
          this.sensorProvider.setupWebsocket();
          this.sensorProvider.setupMessageQueue();
          this.nav.goToRoot({});
        })
      }
    });
  }
}




