import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Platform } from 'ionic-angular';
import {trigger ,transition, style, animate, state} from "@angular/animations"
import { interval } from 'rxjs/observable/interval';
import { take } from 'rxjs/operators';

/**
 * Generated class for the NumberInputPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-number-input',
  templateUrl: 'number-input.html',
  animations: [
    trigger('swipe', [
      state('true', style({ 'background-position-x': "{{ offset }}%" }), { params : { offset : 0.0}}),
      state('false', style({ 'background-position-x': "{{ offset }}%" }), { params : { offset : 0.0}}),
      transition('* => *', [
        animate("0.1s ease-out",
        style({ 'background-position-x': "{{ offset }}%" }))
      ]) 
    ])
  ]
})
export class NumberInputPage {

	title : string;
	value : number;
	animate : boolean = false;
	swipe_offset : number = 0 ;
	screenWidth : number;


  constructor(public navCtrl: NavController, public navParams: NavParams,
  	private viewCtrl : ViewController,
  	private zone : NgZone,
  	private platform : Platform) {
  	this.title = this.navParams.get("title");
  	this.value = this.navParams.get("value");
  	this.screenWidth = this.platform.width();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NumberInputPage');
  }

  back(){
  	this.viewCtrl.dismiss({'save' : false});
  }

  save(){
  	this.viewCtrl.dismiss({'save' : true, 'value' : this.value});
  }

  add(number){
  	this.value+=number;
  	this.swipe_offset += number;
  	this.start_animate();
  }

  swipe(event){
  	
  	var tick = this.swipe_offset < 0 ? 1 : -1;
  	var i;
  	interval(Math.ceil(1000.0/Math.abs(event.deltaX))).pipe( take(Math.abs(event.deltaX))).subscribe(()=> {
  		this.zone.run(()=>{
  				this.add(tick);
  		})
  	});
  }

  private start_animate(){
	this.animate=!this.animate;
  }

  pan(delta,direction) {
  	//console.log(event.deltaX +" "+ event.direction);
  	this.zone.run(()=>{
			this.add(direction*Math.ceil(Math.abs(delta)/this.screenWidth*100));
	})
   }

     scrollComplete(event) {
     	console.log(event);
      // your content here of scroll is finished
     }

}



